body {
  font-family: Afacad, sans-serif;
}

a {
  text-decoration: none;
}

table {
  border-collapse: collapse;
}

td,
th {
  font-weight: 300;
  border: 1px solid #eee;
  padding: 2px;
}

th {
  background: #eee;
  border: 1px solid #ddd;
}

.th-sum {
  background: #f9f9f9;
  border: none;
}

.td-row-num {
  color: #ccc;
  background: none;
  border: none;
}

.number {
  text-align: right;
}

.hidden {
  background: none;
  border: none;
  text-align: center;
}
